import React, { useState } from 'react';
import './LoanModal.scss';
import BasicModal from "../BasicModal";
import DatePicker from 'react-mobile-datepicker';
import {convertDate} from "react-mobile-datepicker/lib/time";

const LoanModal = (props) => {

  const [userData, setUserData] = useState({});

  const [loanStartDate, setLoanStartDate] = useState('');
  const [loanStartDateChanged, setLoanStartDateChanged] = useState(false);
  const [isLoanStartDateOpen, setIsLoanStartDateOpen] = useState(false);

  const [loanTerm, setLoanTerm] = useState(0);
  const [loanTermChanged, setLoanTermChanged] = useState(false);
  const [isLoanTermOpen, setIsLoanTermOpen] = useState(false);

  const [isPrePayment, setIsPrePayment] = useState(false);

  const [loanFinishDate, setLoanFinishDate] = useState('');
  const [loanFinishDateChanged, setLoanFinishDateChanged] = useState(false);
  const [isLoanFinishDateOpen, setIsLoanFinishDateOpen] = useState(false);

  const [loanMoney, setLoanMoney] = useState(0);
  const [loanMoneyChanged, setLoanMoneyChanged] = useState(false);
  const [isLoanMoneyOpen, setIsLoanMoneyOpen] = useState(false);

  const [loanType, setLoanType] = useState(0);

  const [interestRate, setInterestRate] = useState(0);
  const [interestRateChanged, setInterestRateChanged] = useState(false);
  const [isInterestRateOpen, setIsInterestRateOpen] = useState(false);


  /** select btn  */
  const handleSelectBtn  = (e) => {
    switch (e.target.id) {
      case 'loan_start_date_btn':
        setIsLoanStartDateOpen(true);
        break;
      case 'loan_finish_date_btn':
        setIsLoanFinishDateOpen(true);
        break;
      case 'loan_term_btn':
        setIsLoanTermOpen(true);
        break;
      case 'loan_money_btn':
        setIsLoanMoneyOpen(true);
        break;
      case 'interest_rate_btn':
        setIsInterestRateOpen(true);
        break;
      default:
        alert('button click error');
        break;
    }
  };

  /** input handler */
  const handleInput = (e) => {
    switch (e.target.id) {
      case 'loan_term_input':
        setLoanTerm(e.target.value);
        break;
      case 'loan_money_input':
        setLoanMoney(e.target.value);
        break;
      case 'interest_rate_input':
        setInterestRate(e.target.value);
        break;
      default:
        alert('input error');
        break;
    }
  };

  /** loan start date */
  const handleLoanStartDateClose = () => {
    setIsLoanStartDateOpen(false);
  };
  const handleLoanStartDateSelect =  (time) => {
    let date = convertDate(time, 'YYYY-MM-DD');
    setLoanStartDate(date);
    setIsLoanStartDateOpen(false);
    setLoanStartDateChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"loan_start_date": date}); // userData 에 대출시작일 붙이기
    setUserData(oldData);
  };

  /** loan term */
  const handleLoanTermClose = () => {
    setIsLoanTermOpen(false);
  };
  const handleLoanTermSelect = () => {
    setIsLoanTermOpen(false);
    setLoanTermChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"loan_term": loanTerm}); // userData 에 대출기간 붙이기
    setUserData(oldData);
  };

  /** loan pre-payed */
  const prePaymentCheck = (e) => {
    if(e.target.value === 'yes') {
      setIsPrePayment(true);
    } else {
      setIsPrePayment(false);
    }
  };

  /** loan finish date */
  const handleLoanFinishDateClose = () => {
    setIsLoanFinishDateOpen(false);
  };
  const handleLoanFinishDateSelect =  (time) => {
    let date = convertDate(time, 'YYYY-MM-DD');
    setLoanFinishDate(date);
    setIsLoanFinishDateOpen(false);
    setLoanFinishDateChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"loan_finish_date": date}); // userData 에 중도상환일 붙이기
    setUserData(oldData);
  };

  /** loan money */
  const handleLoanMoneyClose = () => {
    setIsLoanMoneyOpen(false);
  };
  const handleLoanMoneySelect = () => {
    setIsLoanMoneyOpen(false);
    setLoanMoneyChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"loan_amount": loanMoney}); // userData 에 대출금액 붙이기
    setUserData(oldData);
  };

  /** loan type */
  const loanTypeCheck = (e) => {
    let oldData = userData;
    setLoanType(parseInt(e.target.value));
    Object.assign(oldData, {"loan_type": parseInt(e.target.value)});
    setUserData(oldData);
  };

  /** interest rate */
  const handleInterestRateClose = () => {
    setIsInterestRateOpen(false);
  };
  const handleInterestRateSelect = () => {
    setIsInterestRateOpen(false);
    setInterestRateChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"interest_rate": interestRate}); // userData 에 이자율 붙이기
    setUserData(oldData);
  };


  const sendData = () => {
    props.closeModal();
    props.callback(userData);
  };

  return(
    <BasicModal
      isModalOpen={props.isModalOpen}
      closeModal={props.closeModal}
      handleSubmit={sendData}
    >
      <div className="modal_title"><p>대출정보</p></div>
      <div id="loan_start_date" className="question_list">
        <div className="question_title">대출시작일</div>
        <div className="question_answer" hidden={!loanStartDateChanged}>{ loanStartDate }</div>
        <div className="select_btn_area">
          <button
            id="loan_start_date_btn"
            className="select-btn"
            onClick={handleSelectBtn}
          >
            날짜 선택
          </button>
          <DatePicker
            id="loan_start_date_picker"
            value={new Date()}
            isOpen={isLoanStartDateOpen}
            theme={'dark'}
            onSelect={handleLoanStartDateSelect}
            onCancel={handleLoanStartDateClose}
            confirmText={'확인'}
            cancelText={'취소'}
            showHeader={true}
          />
        </div>
      </div>

      <div id="loan_term" className="question_list">
        <div className="question_title">대출기간<em>(개월)</em></div>
        <div className="question_answer" hidden={!loanTermChanged}>{ loanTerm }</div>
        <div className="select_btn_area">
          <button
            id="loan_term_btn"
            className="select-btn"
            onClick={handleSelectBtn}
          >
            기간 입력
          </button>
          <BasicModal
            isModalOpen={isLoanTermOpen}
            closeModal={handleLoanTermClose}
            handleSubmit={handleLoanTermSelect}
          >
            <input type="number" id="loan_term_input" onChange={handleInput} placeholder="0"/>
          </BasicModal>
        </div>
      </div>

      <div id="pre_payment" className="question_list">
        <div className="question_title">중도상환여부</div>
        <div className="question_answer">
          <div className="button_area">
            <input type="radio" id="pre_payment_btn1" name="pre_payment_chk" value="yes" checked={isPrePayment === true} onChange={prePaymentCheck}/><p>'Yes'</p>
            <div> | </div>
            <input type="radio" id="pre_payment_btn2" name="pre_payment_chk" value="no" checked={isPrePayment === false} onChange={prePaymentCheck}/><p>'No'</p>
          </div>
        </div>
      </div>

      <div id="loan_finish_date" className="question_list" style={{display: isPrePayment ? 'flex' : 'none'}}>
        <div className="question_title">중도상환일</div>
        <div className="question_answer" hidden={!loanFinishDateChanged}>{ loanFinishDate }</div>
        <div className="select_btn_area">
          <button
            id="loan_finish_date_btn"
            className="select-btn"
            onClick={handleSelectBtn}
          >
            날짜 선택
          </button>
          <DatePicker
            id="loan_finish_date_picker"
            value={new Date()}
            isOpen={isLoanFinishDateOpen}
            theme={'dark'}
            onSelect={handleLoanFinishDateSelect}
            onCancel={handleLoanFinishDateClose}
            confirmText={'확인'}
            cancelText={'취소'}
            showHeader={true}
          />
        </div>
      </div>

      <div id="loan_money" className="question_list">
        <div className="question_title">대출금액<em>(단위: 만원)</em></div>
        <div className="question_answer" hidden={!loanMoneyChanged}>{ loanMoney }</div>
        <div className="select_btn_area">
          <button
            id="loan_money_btn"
            className="select-btn"
            onClick={handleSelectBtn}
          >
            가격 입력
          </button>
          <BasicModal
            isModalOpen={isLoanMoneyOpen}
            closeModal={handleLoanMoneyClose}
            handleSubmit={handleLoanMoneySelect}
          >
            <input type="number" id="loan_money_input" onChange={handleInput} placeholder="0"/>
          </BasicModal>
        </div>
      </div>

      <div id="loan_type" className="question_list">
        <div className="question_title">대출방식</div>
        <div className="question_answer">
          <div className="button_area">
            <span><input type="radio" id="loan_type_btn1" name="loan_type_chk" value="1" checked={loanType === 1} onChange={loanTypeCheck}/><p>'원금 균등 상환'</p></span>
            <span><input type="radio" id="loan_type_btn2" name="loan_type_chk" value="2" checked={loanType === 2} onChange={loanTypeCheck}/><p>'원리금 균등 상환'</p></span>
            <span><input type="radio" id="loan_type_btn3" name="loan_type_chk" value="3" checked={loanType === 3} onChange={loanTypeCheck}/><p>'만기일시 상환'</p></span>
          </div>
        </div>
      </div>

      <div id="interest_rate" className="question_list">
        <div className="question_title">이자율<em>(%)</em></div>
        <div className="question_answer" hidden={!interestRateChanged}>{ interestRate }</div>
        <div className="select_btn_area">
          <button
            id="interest_rate_btn"
            className="select-btn"
            onClick={handleSelectBtn}
          >
            이자율 입력
          </button>
          <BasicModal
            isModalOpen={isInterestRateOpen}
            closeModal={handleInterestRateClose}
            handleSubmit={handleInterestRateSelect}
          >
            <input type="number" id="interest_rate_input" onChange={handleInput} placeholder="0"/>
          </BasicModal>
        </div>
      </div>

    </BasicModal>
  )
};

export default LoanModal;
