import React, { Component } from 'react';
import queryString from 'query-string';
import './FinanceAnalysis.scss';
import AcquisitionModal from "components/modal/AcquisitionModal";
import LoanModal from "components/modal/LoanModal";
import ConfirmModal from "components/modal/ConfirmModal";
import axios from 'axios';

class FinanceAnalysis extends Component{
  state = {
    time: new Date(),
    formOpen: true,
    buttonShape: '+',
    userData: {
      "live": '',
    },
    isAcquisitionModalOpen: false,
    isLoanModalOpen: false,
    isCheckModalOpen: false,
    thirdQuestionOpen: false,
    finalQuestionOpen: false,
    key: ''
  };

  componentDidMount() {
    const query = queryString.parse(window.location.search);
    if(query.key !== undefined) {
      this.setState({
        key: query.key
      });
      console.log(query);
    } else {
      console.log('key error');
    }
  }

  /** 질문 헤더 영역 클릭 이벤트 */
  questionHeaderClick = (e) => {
    if(e.currentTarget.parentNode.querySelector(".button_area").style.display === 'none')
      e.currentTarget.parentNode.querySelector(".button_area").style.display = 'flex';
    else
      e.currentTarget.parentNode.querySelector(".button_area").style.display = 'none';
  };

  /** 첫번째 질문 응답 이벤트 */
  onQuestionOneChanged = (e) => {
    if(e.currentTarget.value === '자가거주') {
      this.setState({
        userData: {"live": "L"},
        thirdQuestionOpen: false,
        finalQuestionOpen: false
      });
    } else if(e.currentTarget.value === '임대') {
      this.setState({
        userData: {"live": "B"},
        thirdQuestionOpen: false,
        finalQuestionOpen: false
      });
    } else  {
      this.setState({
        userData: {"live": "R"},
        thirdQuestionOpen: false,
        finalQuestionOpen: true
      });
    }
    document.querySelector("#question_1").querySelector(".button_area").style.display = 'none';
  };

  /** modal */
  openModal = (e) => {
    if(e.target.id === "q2_acquisition_btn") {
      this.setState({
        isAcquisitionModalOpen: true
      });
    } else if(e.target.id === "q3_loan_btn") {
      this.setState({
        isLoanModalOpen: true
      })
    } else if(e.target.id === "q4_submit_btn") {
      console.log("checkmodal open");
      this.setState({
        isCheckModalOpen: true
      })
    }
  };

  closeModal = () => {
    this.setState({
      isAcquisitionModalOpen: false,
      isLoanModalOpen: false,
      isCheckModalOpen: false
    });
  };

  /** go back */
  goBack = (e) => {
    if(e.currentTarget.id === "q2_back_btn") {  // 2단계에서 이전단계 클릭 시.
      this.setState({
        userData: {"live": ''}
      });
      document.querySelector("#question_1").querySelector(".button_area").style.display = 'flex';
    } else if (e.currentTarget.id === "q3_back_btn") {  // 3단계에서 이전단계 클릭 시.
      this.setState({
        userData: {"live": ''},
        thirdQuestionOpen: false
      });
    } else if(e.currentTarget.id === "q4_back_btn") { // 재무분석 단계에서 처음부터 클릭 시
      // console.log(e.currentTarget);
      this.setState({
        userData: {"live": ''},
        thirdQuestionOpen: false,
        finalQuestionOpen: false
      });
      document.querySelector("#question_1").querySelector(".button_area").style.display = 'flex';
    }
  };

  showForm = () => {
    if(this.state.formOpen) {
      this.setState({
        formOpen: !this.state.formOpen,
        buttonShape: '-'
      });
    } else {
      this.setState({
        formOpen: !this.state.formOpen,
        buttonShape: '+'
      })
    }
  };

  /** callback (save userdata) */
  getAcquisitionData = async (newData) => {
    let userData = this.state.userData;
    Object.assign(userData, newData);

    if(userData.get_loan === 'Y') {
      await this.setState({
        userData: userData,
        thirdQuestionOpen: true,
        finalQuestionOpen: false
      });
    } else {
      await this.setState({
        userData: userData,
        thirdQuestionOpen: false,
        finalQuestionOpen: true
      });
    }
    console.log(JSON.stringify(this.state.userData, null, 2));
    document.querySelector("#question_2").querySelector(".button_area").style.display = 'none';
  };
  getLoanData = async (newData) => {
    let userData = this.state.userData;
    Object.assign(userData, newData);

    await this.setState({
      userData: userData,
      finalQuestionOpen: true
    });
    console.log(JSON.stringify(this.state.userData, null, 2));
    document.querySelector("#question_3").querySelector(".button_area").style.display = 'none';
  };


  /** user data -> API call */
  sendData = async () => {
    alert(JSON.stringify(this.state.userData, null, 2));
    let userData = this.state.userData;
    // const defaultClient = axios.create({withCredentials: true});
    await axios.post(
      "http://apis.zipdata.co/giga/storeUserInfo",
      {
        key: this.state.key,
        live: userData.live,
        acquired_date: userData.acquired_date,
        acquired_price: userData.acquired_price,
        get_loan: userData.get_loan,
        loan_start_date: userData.loan_start_date,
        loan_term: userData.loan_term,
        loan_finish_date: userData.loan_finish_date,
        loan_amount: userData.loan_amount,
        loan_type: userData.loan_type,
        interest_rate: userData.interest_rate
      },

    ).then(res => {
      alert(res.data.msg);
    }).catch(err => {
      alert(err);
    })
  };

  render() {
    let state = this.state;
    return (
      <div className="main-container">
        <div className="form-info__title">
          <div>기본정보 <p>(기가지니에 입력된 주소: <em id="giga_address">서울특별시 강남구 서초동</em>)</p></div>
          <button onClick={this.showForm}>{state.buttonShape}</button>
        </div>

        <div id="question_1" className="question_box">
          <div id="q1_header" className="question_header" onClick={this.questionHeaderClick}>
            <p>1. 입력하신 집의 거주/보유 형태가 어떻게 되나요? <em> (변경 시 처음부터 재입력)</em></p>
          </div>
          <div className="button_area">
            <span><input type="radio" id="q1_btn1" name="q1_chk" value="자가거주" checked={state.userData.live === "L"} onChange={this.onQuestionOneChanged}/><p>자가거주</p></span>
            <span><input type="radio" id="q1_btn2" name="q1_chk" value="임대" checked={state.userData.live === "B"} onChange={this.onQuestionOneChanged}/><p>임대</p></span>
            <span><input type="radio" id="q1_btn3" name="q1_chk" value="전세거주" checked={state.userData.live === "R"} onChange={this.onQuestionOneChanged}/><p>전세거주</p></span>
          </div>
        </div>

        <div id="question_2" className="question_box" hidden={!(state.userData.live === "L" || state.userData.live === "B")}>
          <div id="q2_header" className="question_header" onClick={this.questionHeaderClick}>
            <p>2-1. 분석을 위해 취득 정보를 입력해주세요. </p>
          </div>
          <div className="button_area">
            <button id="q2_acquisition_btn" onClick={this.openModal}>취득 정보 입력</button>
            <button id="q2_back_btn" onClick={this.goBack}>처음부터</button>
          </div>
        </div>
        <AcquisitionModal
          isModalOpen={this.state.isAcquisitionModalOpen}
          closeModal={this.closeModal}
          callback={this.getAcquisitionData}
        />

        <div id="question_3" className="question_box" hidden={!state.thirdQuestionOpen}>
          <div id="q3_header" className="question_header" onClick={this.questionHeaderClick}>
            <p>2-2. 분석을 위해 대출 정보를 입력해주세요. </p>
          </div>
          <div className="button_area">
            <button id="q3_loan_btn" onClick={this.openModal}>대출 정보 입력</button>
            <button id="q3_back_btn" onClick={this.goBack}>처음부터</button>
          </div>
        </div>
        <LoanModal
          isModalOpen={this.state.isLoanModalOpen}
          closeModal={this.closeModal}
          callback={this.getLoanData}
        />

        <div id="question_4" className="question_box" hidden={!state.finalQuestionOpen}>
          <div id="q4_header" className="question_header" onClick={this.questionHeaderClick}>
            <p>3. 입력하신 정보가 맞습니까? 확인버튼을 누르시면 재무분석이 가능합니다.</p>
          </div>
          <div className="button_area">
            <button id="q4_submit_btn" value="yes" onClick={this.openModal}>확인</button>
            <button id="q4_back_btn" value="no" onClick={this.goBack}>처음부터</button>
          </div>
        </div>
        <ConfirmModal
          isModalOpen={this.state.isCheckModalOpen}
          closeModal={this.closeModal}
          callback={this.sendData}
        />
        {/*        <div id="basic-form" className="form-info" hidden={state.formOpen}>
          <div>보유하고 계신 주택의 현재까지의 투자상태를 알아보기 위해 가정...</div>
          <input id="address" type="text" placeholder="주소입력"/>
          <div>
            <input type="radio"/>
            KB 시세
            <input type="text" placeholder="59,000만원 (2019.01월 기준)"/>
          </div>
          <div>
            <input type="radio"/>
            직접 입력
            <input type="text" placeholder="숫자만 입력 가능 (단위: 만원)"/>
          </div>
          <button onClick={this.testAlert}>다음</button>
        </div>*/}
      </div>
    )
  }
}


export default FinanceAnalysis;
