import React, { useState } from 'react';
import './AcquisitionModal.scss';
import BasicModal from "../BasicModal";
import DatePicker from 'react-mobile-datepicker';
import {convertDate} from "react-mobile-datepicker/lib/time";

const AcquisitionModal = (props) => {

  const [userData, setUserData] = useState({});

  const [isDateOpen, setIsDateOpen] = useState(false);
  const [timeChanged, setTimeChanged] = useState(false);
  const [newTime, setNewTime] = useState('');

  const [isMoneyOpen, setIsMoneyOpen] = useState(false);
  const [moneyChanged, setMoneyChanged] = useState(false);
  const [acquiredMoney, setAcquiredMoney] = useState(0);

  const [getLoan, setGetLoan] = useState(null);

  const maxQuestion = 3;
  const [questionCheck, setQuestionCheck] = useState(0);

  /** Date modal */
  const handleDateClick = () => {
    setIsDateOpen(true);
  };
  const handleDateCancel = () => {
    setIsDateOpen(false);
  };
  const handleDateSelect = (time) => {
    let date = convertDate(time, 'YYYY-MM-DD');
    setNewTime(date);
    setIsDateOpen(false);
    setTimeChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"acquired_date": date}); // userData 에 취득일자 붙이기
    setUserData(oldData);
  };

  /** Money modal */
  const handleMoneyClick = () => {
    setIsMoneyOpen(true);
  };
  const handleMoneyCancel = () => {
    setIsMoneyOpen(false);
  };
  const handleMoneySelect = () => {
    setIsMoneyOpen(false);
    setMoneyChanged(true);

    let oldData = userData;
    Object.assign(oldData, {"acquired_price": acquiredMoney}); // userData 에 취득가 붙이기
    setUserData(oldData);
  };
  const inputMoney = (e) => {
    setAcquiredMoney(e.target.value);
  };

  /** Loan */
  const isLoanChanged = (e) => {
    let oldData = userData;

    if(e.target.value === 'yes') {
      setGetLoan(true);
      Object.assign(oldData, {"get_loan": 'Y'}); // userData 에 대출여부 붙이기
    } else {
      setGetLoan(false);
      Object.assign(oldData, {"get_loan": 'N'}); // userData 에 대출여부 붙이기
    }
    setUserData(oldData);
  };

  // const checkQuestion = () => {
  //   setQuestionCheck(questionCheck+1);
  //   if(questionCheck >= maxQuestion) {
  //   }
  //   return questionCheck === maxQuestion;
  // };

  const sendData = () => {
    props.closeModal();
    props.callback(userData);
  };

  const getMarketPrice = () => {
    alert('API CALL 발동!!!')
  };


  return (
    <BasicModal
      isModalOpen={props.isModalOpen}
      closeModal={props.closeModal}
      handleSubmit={sendData}
    >
      <div className="modal_title"><p>취득정보</p></div>
      <div id="acquire_date" className="question_list">
        <div className="question_title">취득일</div>
        <div className="question_answer" hidden={!timeChanged}>{ newTime }</div>
        <div className="select_btn_area">
          <button
            className="select-btn"
            onClick={handleDateClick}
          >
            날짜 선택
          </button>
          <DatePicker
            value={new Date()}
            isOpen={isDateOpen}
            theme={'dark'}
            onSelect={handleDateSelect}
            onCancel={handleDateCancel}
            confirmText={'확인'}
            cancelText={'취소'}
            showHeader={true}
          />
        </div>
      </div>

      <div id="acquire_money" className="question_list">
        <div className="question_title">취득가<em>(단위: 만원)</em></div>
        <div className="question_answer" hidden={!moneyChanged}>{ acquiredMoney }</div>
        <div className="select_btn_area">
          <button
            className="select-btn"
            onClick={handleMoneyClick}
          >
            가격 입력
          </button>
          <BasicModal
            isModalOpen={isMoneyOpen}
            closeModal={handleMoneyCancel}
            handleSubmit={handleMoneySelect}
            plusBtn="취득 당시 시세조회"
            plusBtnOnclick={getMarketPrice}
          >
            <input type="number" id="acquired_money" onChange={inputMoney} placeholder="0"/>
          </BasicModal>
        </div>
      </div>

      <div id="loan_tab" className="question_list">
        <div className="question_title">대출여부</div>
        <div className="question_answer">
          <div className="button_area">
            <input type="radio" id="loan_btn1" name="loan_chk" value="yes" checked={getLoan === true} onChange={isLoanChanged}/><p>'Yes'</p>
            <div> | </div>
            <input type="radio" id="loan_btn2" name="loan_chk" value="no" checked={getLoan === false} onChange={isLoanChanged}/><p>'No'</p>
          </div>
        </div>
      </div>

    </BasicModal>
  )
};

export default AcquisitionModal;
