import React from 'react';
import './BasicModal.scss';

const BasicModal = (props) => {
  return (
    <div id="modal_outer" style={{display: props.isModalOpen ? "block" : "none"}}>
      <div id="modal_overlay" onClick={props.closeModal} />
      <div onClick={props.closeModal} />
      <div id="modal">
        {props.children}
        <div className="button_area">
          <button id="plus_btn" style={{display: props.plusBtn ? "block" : "none"}} onClick={props.plusBtnOnclick}>{props.plusBtn}</button>
          <button onClick={props.closeModal}>취소</button>
          <button onClick={props.handleSubmit}>확인</button>
        </div>
      </div>
    </div>
  )
};

export default BasicModal;
